// jQueryはcdnで読み込み
// @types/jqueryを読み込んでいるのでエラーは出ないようにしている
$(function() {

    class Member {
        // クラス変数
        static sequenceId: number = 1;
        static digitId: number = 5;

        // メンバ変数
        private id: string;
        private name: string;

        // コンストラクタ
        constructor(name: string = 'gest') {
            this.id = (Member.sequenceId++).toString();
            this.name = name;
        }

        // 自己紹介メソッド
        getMyData() {
            console.log(`私の名前は [${this.name}] です。`);
            console.log(`私のIDは [${this.id}] です。`);
        }
    }

    // インスタンス生成
    let taro: Member = new Member('test taro');
    // taro.getMyData();

    let hanako: Member = new Member('test hanako');
    // hanako.getMyData();

    let gest: Member = new Member();
    // gest.getMyData();
});
